@media screen and (max-width: 480px) {
  .table-action thead {
    display: none;
  }
  .table-action td {
    border: 0;
  }
  .table-action td:not(:last-child) {
    display: block;
  }
  .table-action td:nth-child(1)::before {
    content: 'Nome: '
  }
  .table-action td:nth-child(2)::before {
    content: 'Sexo: '
  }
  .table-action td:nth-child(3)::before {
    content: 'Nascimento: '
  }
  .table-action td:nth-child(4)::before {
    content: 'Foto: '
  }
  .table-action td::before {
    font-weight: bold;
    color: blue;
    display: inline-block;
    width: 70px;
  }
}